}

input.error,
textarea.error,
select.error {
  border-color: red;
}

#submitButton {
  padding: 12px;
  background-color: #FEC938;
  color: white;
  border: none;
  border-radius: 4px;
  font-size: 16px;
  cursor: pointer;
  width: 100%;
  margin: 0 auto;
  transition: background-color 0.3s ease;
}

#submitButton:hover {
