  }

  100% {
    transform: translate(2000px, -1000px);
  }
}

.dropdown {
  position: relative;
  width: 100%;
  margin-bottom: 20px;
}

.dropdown-toggle {
  width: 100%;
  padding: 10px;
  border: 1px solid #444;
  border-radius: 4px;
  font-size: 16px;
  cursor: pointer;
  background-color: #333;
  color: #888888;
  text-align: left;
  transition: background-color 0.3s, border-color 0.3s, color 0.3s;
  box-sizing: border-box;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.dropdown-menu.active {
  display: block;
}

.dropdown-item input[type="checkbox"] {
  margin-right: 8px;
  margin-left: 0;
  width: 14px;
  height: 16px;
}

.dropdown-toggle::after {
  content: '▼';
  font-size: 12px;
  color: #888888;
  position: absolute;
  right: 5px;
  top: 50%;
  transform: translateY(-50%);
  pointer-events: none;
}

.dropdown-menu {
  position: absolute;
  top: 100%;
  left: 0;
  width: auto;
  min-width: 100%;
  max-height: 300px;
  overflow-y: auto;
  background-color: #414141;
  border: 1px solid #444;
  border-radius: 4px;
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.3);
  display: none;
  z-index: 1000;
  margin-top: 5px;
  white-space: nowrap;
  scrollbar-width: thin;
}

.dropdown-item {
  display: block;
  width: auto;
  padding: 10px;
  font-size: 16px;
  background-color: #414141;
  color: #e0e0e0;
  transition: background-color 0.3s, color 0.3s;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.dropdown-item:hover {
  background-color: #282828;
}

.dropdown-menu::-webkit-scrollbar {
  width: 8px;
}

.dropdown-menu::-webkit-scrollbar-track {
  background: #333;
}

.dropdown-menu::-webkit-scrollbar-thumb {
  background-color: #888;
  border-radius: 10px;
  border: 2px solid #333;
}

.dropdown-menu {
  scrollbar-width: thin;
  scrollbar-color: #888 #333;
}

@media (max-width: 768px) {
  body {
    background-color: #2c2c2c;
  }

  #formContainer {
    width: 100%;
    height: 90vh;
    max-height: 90vh;
    overflow-y: auto;
    padding: 20px;
    box-sizing: border-box;
    box-shadow: none;
    border-radius: 0;
  }

  #registrationForm {
    width: 100%;
    margin: 0 auto;
    margin-top: 20px;
  }

  .title {
    font-size: 22px;
    margin-bottom: 20px;
    margin-top: 10px;
  }

  input,
  textarea,
  select {
    font-size: 14px;
    padding: 10px 5px;
  }

  #cargo,
  #ship,
  #FullNameTalman,
  #TalmanStatus {
    height: 40px;
  }

  .dropdown-toggle {
    font-size: 14px;
  }

  .dropdown-menu {
    max-height: 200px;
    overflow-y: auto;
  }

  .logo {
    width: 120px;
    margin-bottom: 20px;
  }

  #submitButton {
    padding: 10px;
    font-size: 14px;
    margin-bottom: 50px;
  }
}